@charset "utf-8";
/* CSS Document */

/* fonts */
/*Gotham Pro*/
@font-face {
	font-family: 'Gotham Pro';
	src: url('../fonts/GothamPro-Medium.eot');
	src: local('Gotham Pro Medium'), local('GothamPro-Medium'),
		url('../fonts/GothamPro-Medium.eot?#iefix') format('embedded-opentype'),
		url('../fonts/GothamPro-Medium.woff') format('woff'),
		url('../fonts/GothamPro-Medium.ttf') format('truetype');
	font-weight: 500;
	font-style: normal;
}
@font-face {
	font-family: 'Gotham Pro';
	src: url('../fonts/GothamPro-Bold.eot');
	src: local('Gotham Pro Bold'), local('GothamPro-Bold'),
		url('../fonts/GothamPro-Bold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/GothamPro-Bold.woff') format('woff'),
		url('../fonts/GothamPro-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}
@font-face {
	font-family: 'Gotham Pro';
	src: url('../fonts/GothamPro.eot');
	src: local('Gotham Pro'), local('GothamPro'),
		url('../fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
		url('../fonts/GothamPro.woff') format('woff'),
		url('../fonts/GothamPro.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

/*Gotham Pro*/
@font-face {
	font-family: 'Futura PT Bold';
	src: url('../fonts/Futura PT Bold.eot');
	src: local('☺'), url('../fonts/Futura PT Bold.woff') format('woff'), url('../fonts/Futura PT Bold.ttf') format('truetype'), url('../fonts/Futura PT Bold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Futura PT Demi';
	src: url('../fonts/Futura PT Demi.eot');
	src: local('☺'), url('../fonts/Futura PT Demi.woff') format('woff'), url('../fonts/Futura PT Demi.ttf') format('truetype'), url('../fonts/Futura PT Demi.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Futura PT Heavy';
	src: url('../fonts/Futura PT Heavy.eot');
	src: local('☺'), url('../fonts/Futura PT Heavy.woff') format('woff'), url('../fonts/Futura PT Heavy.ttf') format('truetype'), url('../fonts/Futura PT Heavy.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

/* reset */
@mixin clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}
$color-1: #6091f8;
$color-2: #78b0f9;
$color-3: #fac476;
$color-4: #b066fe;
$font-1: 'Gotham Pro', sans-serif;
$font-2: 'Futura PT Heavy', serif;
$font-3: 'Futura PT Bold', serif;
$font-4: 'Futura PT Demi', serif;

/**/
*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: $color-1;
	text-decoration: none;
	transition-duration: 0.2s;
}
a:hover, a:focus{
	text-decoration: none;
	color: $color-2;
}
a:focus{
 	text-decoration: none;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(250, 196, 118, 0.8); }

*::selection {
  background: rgba(250, 196, 118, 0.8); }

html{
	height: 100%;
	overflow-y: scroll;
}
body{
	font: 14px $font-1;
	color: #464b50;
	background: #fff; 
	padding: 0;
	min-height: 100%;
}
#wrapper{
	overflow: hidden;
	position: relative;
}

@media (min-width: 1200px){
	.container {
	    max-width: 1200px;
	}
}

.alignright {float: right; margin: 5px 0 25px 30px;}
.alignleft {float: left; margin: 5px 30px 25px 0;}
.aligncenter {
	display: block;
	margin: 25px auto 25px;
	text-align: center;
}

h1, h2, h3, h4, .h1, .h2, .h3, .h4, .big-title{
	margin-top: 0px;
    margin-bottom: 0.8em;
	font-weight: normal;
	font-family: $font-2;
}
.big-title{
	font-size: 50px;
	font-family: $font-2;
	line-height: 1.1em;
	margin-bottom: 0.6em;
}
h1.big-title{
	margin-bottom: 0.8em;
}
h5, h6, .h5, .h6{
	margin-top: 0;
	margin-bottom: 0.8em;
	font-family: $font-2;
	text-transform: none;
}

h1, .h1{
	font-size: 40px;
	margin-bottom: 1em;
}
h2, .h2{
	font-size: 30px;
	font-family: $font-4;
}
h3, .h3{
	font-size: 24px;
	font-family: $font-4;
}
h4, .h4{
	font-size: 16px;
	font-family: $font-3;
}
h5, .h5{
	font-size: 14px;
	font-family: $font-1;
}

.title-arrow{
	padding-left: 35px;
	position: relative;
	&:before{
		content: '';
		position: absolute;
	    left: 0px;
	    top: 2px;
	    border: 17px solid transparent;
	    border-left-color: $color-1;
	    border-top-width: 17px;
	    border-bottom-width: 17px;
	}
}

ul, ol{
	list-style-position:inside;
	margin-bottom:0.8em;
	}
	
	ol li{
		margin-bottom:0.8em;
		}

p{
    margin: 0 0 0.8em;
}

.article{
	line-height: 1.6em;
	a{
		text-decoration: underline;
		&:hover{
			color: $color-4;
			text-decoration: underline;
		}
	}
	p +, ol +, ul + {
		h3, .h3{
			margin-top: 0.8em;
		}
	}
	ol, ul{
		margin-bottom: .8em;
	}
	.date{
		font-size: 16px;
	}
}

.article ul li{
	list-style: none;
	margin-bottom: 0.4em;
	padding-left: 17px;
	position: relative;
	&:before{
		content: "";
	    position: absolute;
	    top: 6px;
	    left: 0px;
	    border: 5px solid transparent;
	    border-left-color: $color-1;
	}
}
.article ol{
	counter-reset: myCounter;
	li{
		list-style: none;
		margin-bottom: 0.4em;
		padding-left: 25px;
		position: relative;
		&:before{
			counter-increment: myCounter;
	        content: counter(myCounter) ".";
	        color: $color-1;
	        font-weight: 500;
	        float: left;
	        margin: 0px 0px 0 0;
	        position: absolute;
	        left: 0;
	        top: 0;
		}
	}
}

.breadcrumbs{
	font-size: 16px;
	margin-bottom: 55px;
	a{
		color: #3c3c46;
		display: inline-block;
		margin-right: 10px;
		vertical-align: middle;
		&:hover span{
			color: $color-1;
		}
		&:after{
			content: '';
			width: 7px;
			height: 7px;
			display: inline-block;
			margin: 3px 0 0 10px;
			background: $color-1;
			border-radius: 50%;
			vertical-align: middle;
		}
	}
	span{
		color: #3c3c46;
		display: inline-block;
		vertical-align: middle;
		transition-duration: 0.2s;
	}
}

.pagination{
	text-align: center;
	display: block;
	padding: 50px 0 30px;
	ul{
		display: block;
		margin: 0;
		padding: 0;
		li{
			display: inline-block;
			margin: 0 8px;
			padding: 0;
			&:before{
				display: none;
			}
		}
	}
	.btn{
		margin: 0 0px 0 0 !important;
	    padding: 0;
	    width: 40px;
	    height: 40px;
	    min-height: 0;
	    padding: 0;
	    border: 2px solid $color-1;
	    box-shadow: none;
	    color: $color-1;
	    font-weight: 500;
	    font-size: 26px;
	    line-height: 36px;
	    text-align: center;
	    text-decoration: none;
	    &:before{
	    	background-color: #fff;
	    }
	}
	.btn:hover{
    	color: #fff;
    	text-decoration: none;
	    .hover{
	    	width: 80px;
	    	height: 80px;
	    	background-color: $color-1;
	    }
	}
	span.btn{
		color: #fff;
    	text-decoration: none;
    	background-color: $color-1;
    	&:before{
	    	display: none !important;
	    }
    	.hover{
    		display: none !important;
    	}
	}
}

/* table */
table{
	border-collapse:collapse;
	width:100%;
}

table tr:nth-child(2n+1){
	background-color: #f5f5f5;
}
	
table th{
	padding: 25px 35px;
	background-color: #fff;
	text-transform: uppercase;
	text-align: left;
	font-size: 14px;
	font-weight: 500;
	border-bottom: 2px solid $color-1;
	}
	
table td{
	padding: 17px 35px;
	font-size: 16px;
	}

/**/
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
	background: none;
	border: none;
	border-bottom: 2px solid #e6e6e6;
	margin-bottom: 10px;
	width: 100%;
	height: 54px;
	padding: 0 0px;
	font-family: $font-1;
	font-size: 14px;
	border-radius: 0;
	display: block;
	color: #464b50;
	box-sizing: border-box;
	&:focus{
		border-bottom-color: $color-1;
	}
	&.error{
		border-bottom-color: #f24645;
	}
}
textarea{
	padding: 17px 0 5px 0px;
	height: 100px;
	resize: none;
}
label{
	font-weight: normal;
}
form{
	p{
		margin: 0;
	}
}
.input-wrap{
	position: relative;
	.icon{
		position: absolute;
		right: 0px;
		top: 50%;
		margin-top: -9px;
		width: 18px;
		height: 18px;
		svg{
			width: 100%;
			height: 100%;
			fill: #e6e6e6;
		}
	}
	input:focus + .icon svg{
		fill: $color-1;
	}
	input.error + .icon svg{
		fill: #f24645;
	}
	.error-message{
		position: absolute;
		right: 0;
		bottom: -14px;
		color: #f24645;
		font-size: 11px;
	}
}
	
/**/	
::-webkit-input-placeholder {color:#969696;transition-duration:0.2s;}
::-moz-placeholder          {color:#969696;transition-duration:0.2s;}
:-moz-placeholder           {color:#969696;transition-duration:0.2s;}
:-ms-input-placeholder      {color:#969696;transition-duration:0.2s;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

/* button */
.btn{
	display: inline-block;
	min-height: 60px;
	line-height: 60px;
	padding: 0px 40px;
	margin: 30px 0 10px;
	color: #fff;
	background: none;
	border: none;
	font-size: 14px;
	font-weight: 500;
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
	cursor: pointer;
	border-radius: 30px;
	box-shadow: 0 16px 40px 0 rgba(96, 145, 248, 0.5);
	transition-duration: 0.2s;
	overflow: hidden;
	position: relative;
	z-index: 1;
	&:before{
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: $color-1;
		z-index: -2;
	}
	.hover {
		position: absolute;
	    width: 0;
		height: 0;
		border-radius: 50%;
		background-color: $color-2;
		transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
		transform: translate(-50%, -50%);
		z-index: -1;
	}
	&:hover {
		color: #fff;
		box-shadow: none;
		.hover {
			width: 800px;
    		height: 800px;
		}
	}
	&:focus, &:active{
		outline: none;
		color: #fff;
		background-color: $color-2;
		box-shadow: inset 0 3px 0 0 $color-1;
		&:before{
			display: none;
		}
		.hover{
			display: none;
		}
	}
	&.next, &.prev{
		&:focus, &:active{
			box-shadow: none;
			background: none;
			&:before{
				display: block;
			}
			.hover{
				display: block;
			}
		}
	}
}

.btn.btn-border{
	color: $color-1;
	border: 2px solid $color-1;
	box-shadow: none;
	&:before{
		background: #fff;
	}
	.hover{
		background-color: $color-1;
	}
}

.btn.btn-border-white{
	color: #fff;
	border: 2px solid #fff;
	box-shadow: none;
	&:before{
		background: none;
	}
	.hover{
		background-color: #fff;
	}
	&:hover{
		color: $color-1;
	}
	&:focus, &:active{
		color: $color-1;
		background-color: #fff;
		box-shadow: none;
		&:before{
			display: none;
		}
		.hover{
			display: none;
		}
	}
	&.btn-arrow:hover, &.btn-arrow:focus{
		&:after{
			background-image: url('../images/arrow-blue.svg');
		}
	}
}

.btn.btn-arrow{
	&:after{
		content: '';
		width: 21px;
		height: 12px;
		background: url('../images/arrow.svg') center right no-repeat;
		display: inline-block;
		background-size: cover;
		margin-left: 19px;
		vertical-align: middle;
		transition-duration: 0.2s;
	}
}

.btn-play{
	display: inline-block;
	line-height: 36px;
	color: $color-1;
	font-weight: 500;
	text-decoration: none;
	position: relative;
	&:before{
		content: '';
		display: inline-block;
		width: 36px;
		height: 36px;
		border-radius: 50%;
		background-color: $color-1;
		transition-duration: 0.2s;
		float: left;
		margin-right: 10px;
	}
	&:after{
		content: '';
		position: absolute;
		left: 14px;
    	top: 10px;
		border: 10px solid transparent;
		border-left-color: #fff;
		border-top-width: 8px;
		border-bottom-width: 8px; 
	}
	&:hover{
		color: $color-2;
		&:before{
			background-color: $color-2;
		}
	}
}

.btn.btn-auth{
	display: inline-block;
	min-height: 50px;
	line-height: 46px;
	padding: 0px 66px 0 20px;
	margin: 0;
	color: #fff;
	background: none;
	border: 2px solid #fff;
	font-size: 14px;
	font-weight: 500;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	border-radius: 30px;
	transition-duration: 0.2s;
	position: relative;
	.icon{
		content: '';
		position: absolute;
		right: 1px;
		top: 1px;
		width: 44px;
		height: 44px;
		border: none;
		border-radius: 50%;
		display: flex;
		background: -moz-linear-gradient(-45deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
		background: -webkit-linear-gradient(-45deg, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
		background: linear-gradient(135deg, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 );
		svg{
			width: 20px;
			height: 20px;
			margin: auto;
			fill: $color-1;
		}
	}
	&:hover{

	}
}

.btn.btn-next, .btn.btn-prev{
	margin: 0 20px 0 0;
    padding: 0;
    width: 40px;
    height: 40px;
    min-height: 0;
    border: 2px solid $color-1;
    box-shadow: none;
    &:before{
    	background-color: #fff;
    }
    &:after{
	    content: '';
	    position: absolute;
	    z-index: 5;
	    left: 50%;
	    top: 50%;
	    margin: -7px 0 0 -7px;
	    width: 14px;
	    height: 14px;
	    background: url(../images/arrow-blue.svg) center right no-repeat;
	    background-size: 26px 16px;
	    transition-duration: 0.3s;
    }
    &:hover{
    	&:after{
    		background-image: url(../images/arrow.svg);
    	}
	    .hover{
	    	width: 80px;
	    	height: 80px;
	    	background-color: $color-1;
	    }
	}
}

.btn.btn-prev{
	&:after{
		transform: rotate(180deg);
	}
}

.link-arrow{
	font-weight: 500;
	font-size: 14px;
	padding-right: 41px;
	position: relative;
	&:after{
		content: '';
	    width: 20px;
	    height: 12px;
	    position: absolute;
	    left: 100%;
	    top: 50%;
	    margin: -6px 0 0 -31px;
	    background: url(../images/arrow-blue-long.svg) center right no-repeat;
	    background-size: 30px 12px;
	    display: inline-block;
	    vertical-align: middle;
	    transition-duration: 0.2s;
	}
	&:hover{
		color: $color-4;
		&:after{
			width: 30px;
			background-image:  url(../images/arrow-purple.svg);
		}
	}
}
.link{
	font-weight: 500;
	font-size: 14px;
	span{
		display: block;
		&.show-text{
			display: none;
		}
		&.hide-text{
			display: block;
		}
	}
	&.collapsed span{
		&.hide-text{
			display: none;
		}
		&.show-text{
			display: block;
		}
	}
}

.sticky .btn-auth{
	overflow: hidden;
	width: 50px;
	height: 50px;
	padding: 0;
	border-color: $color-1;
	.icon{
		background: $color-1;
		svg{
			fill: #fff;
		}
	}
	&:hover{
		background-color: $color-2;
		border-color: $color-2;
		.icon{
			background: $color-2;
		}
	}
}

/* nav-tabs */
.nav-tabs{
	border: none;
	text-align: center;
	justify-content: center;
	li{
		display: inline-block;
		margin: 0 2px;
		a{
			padding: 15px 25px;
			border-radius: 30px;
			border: 2px solid transparent;
			text-align: center;
			position: relative;
			font-weight: 500;
			color: #5f6468;
			display: block;
			&:hover{
				color: $color-1;
			}
			&:after{
				content: '';
				position: absolute;
				left: 50%;
				margin: 2px 0 0 -3px;
				top: 50%;
				border: 3px solid transparent;
				border-top-color: $color-1;
				opacity: 0;
				transition-duration: 0.2s;
			}
			&.active{
				border-color: $color-1;
				color: $color-1;
				&:after{
					top: 100%;
					opacity: 1;
				}
			}
		}
	}
}

/* pop up */
.modal-open{
	padding-right: 0 !important;
}

.modal-backdrop{
	background-color: #303030;
}
.modal-backdrop.in {
    opacity: .85;
    filter: alpha(opacity=85);
}

.modal-open .modal.show{
	display: flex !important;
}

.modal-dialog{
	width: 395px;
	margin: auto;
}
.modal-content{
	background: #fff;
	border: 2px solid $color-3;
	box-shadow: none;
	border-radius: 0px;
	padding: 45px 40px;
	text-align: center;
	position: relative;
	font-size: 18px;
}

.modal button.close {
	position: absolute;
	top: 12px;
	right: 12px;
	width: 20px;
	height: 20px;
	margin: 0;
	opacity: 1;
	background: url('../images/close.svg') center center no-repeat;
	background-size: cover;
	&:hover{
		opacity: 0.8;
	}
}

.compensate-for-scrollbar {
    margin-right: 0 !important;
}

@media only screen and (max-width: 1230px) {

}			
@media only screen and (max-width:991px) {
	.btn{
		padding: 0 20px;
	}
	table th{
		padding: 20px 20px;
		border-bottom: 1px solid $color-1;
		}
	table td{
		padding: 15px 20px;
		font-size: 14px;
	}
	.nav-tabs{
		justify-content: flex-start;
	}
	.pagination{
		padding: 35px 0 15px;
		ul{
			li{
				margin: 0 5px;
			}
		}
		.btn{
			font-size: 20px;
			margin-bottom: 5px !important;
		}
	}
}
@media only screen and (max-width:767px) {
	.big-title{
		font-size: 36px;
	}
	h1, .h1{
		font-size: 28px;
	}
	h2, .h2{
		font-size: 24px;
	}
	.title-arrow:before {
	    top: -3px;
	}
	table th{
		padding: 20px 10px;
		border-bottom: 1px solid $color-1;
		}
	table td{
		padding: 15px 10px;
		}
	.modal-dialog{
		max-width: 320px;
	}
	.modal-content{
		margin: 0 10px;
	}
	.btn.btn-auth{
		overflow: hidden;
