/* HEADER */
#header{
	-webkit-transform: translate3d(0, 0, 0);
	transition-duration: 0.2s;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 100;
	padding: 20px 0;
	.header-top{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;
	}
	.logo{
		float: left;
		max-width: 273px;
	}
	.contacts{
		float: right;
		font-size: 18px;
		p{
			display: inline-block;
			margin-left: 35px;
			margin-bottom: 0;
		}
		a{
			color: #fff;
			&:hover{
				text-decoration: underline;
			}
		}
		.icon{
			width: 20px;
			height: 20px;
			display: inline-block;
			vertical-align: middle;
			margin-right: 8px;
			svg{
				fill: #fff;
			}
		}
	}
}

.inside #header:after{
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: url(../images/bg-first.jpg) center -20px no-repeat;
	z-index: -1;
}
.sticky.inside #header:after{
	background: #fff;
}

.header-bottom{
	clear: both;
	display: flex;
	align-items: center;
	justify-content: space-between;
	.contacts{
		opacity: 0;
		overflow: hidden;
		width: 0;
		height: 0;
	}
	.language{
		padding: 0 15px;
		ul{
			padding: 0;
			margin: 0;
			list-style: none;
			li{
				float: left;
				position: relative;
				padding: 0 7px;
				&:after{
					content: '';
					position: absolute;
					left: -1px;
					top: 50%;
					width: 2px;
					height: 12px;
					margin-top: -6px;
					background-color: #98d0ff;
				}
				&:first-child:after{
					display: none;
				}
				a{
					color: #fff;
					font-weight: 500;
					font-size: 14px;
					text-transform: uppercase;
					&.active{
						border-bottom: 2px solid #fff;
					}
				}
			}
		}
	}
}

/* NAV */
.nav-trigger{
	display: none;
	position: relative;
	width: 50px;
	height: 50px;
	cursor: pointer;
	float: left;
	position: absolute;
	left: 0;
	top: 0;
	span{
		width: 50px;
		height: 3px;
		background: $color-1;
		border-radius: 5px;
		position: absolute;
		top: 0;
		left: 0;
		transition-duration: 0.2s;
	}
	span{
		left: 0;
		top: 13px;
		&:nth-child(2){
			top: 24px;
		}
		&:nth-child(3){
			top: 35px;
		}
	}
	&:hover{
		span{
			background-color: $color-2;
		}
	}
	&.active{
		span{
			transform-origin: center center;
			transform: rotate(45deg);
			top: 23px;
			&:nth-child(2){
				opacity: 0;
				transform: rotate(0);
			}
			&:nth-child(3){
				transform: rotate(-45deg);
				top: 23px;
			}
		}
	}
}

#nav{
	ul{
		padding: 0;
		margin: 0;
		list-style: none;
	}
	li{
		display: inline-block;
		margin: 0 23px;
		a{
			display: block;
			font-size: 16px;
			color: #3c3c46;
			text-align: center;
			padding: 16px 0;
			text-decoration: none;
			position: relative;
			&:hover, &.active{
				color: $color-1;
			}
		}
		&:first-child{
			margin-left: 0;
		}
		&:nth-child(5), &:nth-child(6){
			a{
				color: #fff;
				transition-duration: 0.5s;
				&:hover{
					color: #333;
				}
			}
		} 
	}
}


.sticky #header{
	background-color: #fff;
	box-shadow: 0 15px 18px 0 rgba(0, 0, 0, 0.06);
	padding: 0px 0;
	.header-top{
		display: none;
	}
	#nav{
		li{
			margin: 0 8px;
			a{
				padding: 32px 0;
				color: #3c3c46;
				font-size: 14px;
				&:hover{
					color: $color-1;
				}
				&.active{
					color: $color-1;
					&:after{
						content: '';
						position: absolute;
						left: 0;
						bottom: 0;
						width: 100%;
						height: 3px;
						background-color: $color-1;
					}
				}
			}
			&:first-child{
				margin-left: 0;
			}
		}
	}
	.language{
		ul{
			li{
				&:after{
					background-color: #3c3c46;
				}
				a{
					color: #3c3c46;
					&.active{
						border-bottom: 2px solid $color-1;
					}
				}
			}
		}
	}
	.header-bottom{
		.contacts{
			color: #3c3c46;
			font-size: 14px;
			animation-name: displayBlock; 
			animation-duration: 0s;
			animation-delay: 0.2s;
			animation-fill-mode: both;
			a{
				color: #3c3c46;
			}
			p{
				margin-left: 20px;
			}
			.icon{
				width: 16px;
				height: 16px;
				margin-right: 5px;
				svg{
					fill: $color-1;
				}
			}
		}
	}
}

@keyframes displayBlock {
	0% {
	    opacity: 1;
	    width: 0;
		height: 0;
	}
 	100% {
	    opacity: 1;
	    width: auto;
		height: auto;
	}
}

