			z-index: 10;
			box-shadow: 0 10px 25px 0 rgba(250, 196, 118, .2); 
			&:before{
				content: '';
				position: absolute;
				border: 5px solid transparent;
				border-top: 15px solid $color-3;
				top: 100%;
				left: 50%;
				margin-left: -5px;
			}
			&:after{
				content: '';
