				z-index: 5;
			}
			span{
				background: #b7d6ff;
				z-index: 3;
				width: 1px;
				height: 500px;
				position: absolute;
				top: 68px;
				left: 50%;
			}
			&:nth-child(1){
				top: 283px;
				animation-delay: 1s;
			}
			&:nth-child(2){
				top: 271px;
				animation-delay: 1.1s;
			}
			&:nth-child(3){
				top: 333px;
				animation-delay: 1.2s;
			}
			&:nth-child(4){
				top: 232px;
				animation-delay: 1.3s;
			}
			&:nth-child(5){
				top: 206px;
				left: -4px;
				animation-delay: 1.4s;
			}
			&:nth-child(6){
				top: 253px;
				left: -4px;
				animation-delay: 1.5s;
			}
			&:nth-child(7){
				top: 166px;
				animation-delay: 1.6s;
			}
			&:nth-child(8){
				top: 134px;
				animation-delay: 1.7s;
			}
			&:nth-child(9){
				top: 84px;
				animation-delay: 1.8s;
			}
			&:nth-child(10){
				top: 118px;
				animation-delay: 1.9s;
			}
			&:nth-child(11){
				top: 153px;
				animation-delay: 2s;
			}
